.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgb(46, 46, 46);
  width: 100vw;
  height: 7vh;
}
.navbarLeft {
  display: flex;
  margin: 0 1rem;
  height: 100%;
  width: 20%;
}

.navbarLogo {
  height: 100%;
  width: 10%;
}

.navbarLink {
  color: antiquewhite;
  padding: 1rem;
  margin: 0 1rem;
  text-decoration: none;
  cursor: pointer;
}

.navbarLink:hover {
  background: rgba(75, 75, 75, 0.418);
}

.navbarBtn {
  color: antiquewhite;
  padding: 1rem;
  margin: 0 1rem;
  cursor: pointer;
  border: none;
  background: transparent;
}

.navbarBtn:hover {
  background: rgba(75, 75, 75, 0.418);
}
